body {
  width: 100vw;
  height: 100vh;
  /* overflow: hidden; */
  background: linear-gradient(#4cb8c4, #3cd3ad);
  display: flex;
  justify-content: center;
  align-items: center;
}

.index-container {
  text-align: center;
  display: flex;
  flex-direction: column;
}

.index-container h1 {
  color: #ff0000;
}

.index-container p {
  color: #00004d;
  font-weight: bold;
}

.index-container .instructions {
  color: black;
  font-weight: normal;
  text-align: left;
}
.link {
  padding: 1rem 2rem;
  border: 1px solid #00004d;
  text-decoration: none;
  background-color: #00004d;
  color: white;
  cursor: pointer;
}

.link:hover {
  background-color: #000030;
}

.quiz-content {
  width: 45rem;
  margin: auto;
  padding: 1rem;
  background-color: rgba(204, 204, 204, 0.205);
  box-shadow: 1px 2px 5px rgb(95, 93, 93);
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.question {
  padding: 0.2rem 1rem;
  background-color: rgb(255, 255, 255);
  border: 1px solid #ccc;
  color: rgb(44, 42, 42);
  font-weight: 500;
  display: flex;
  flex-direction: column;
}

.options {
  display: flex;
  gap: 0.5rem;
}

.options button {
  padding: 0.4rem 0.6rem;
  background-color: rgb(213, 228, 2);
  font-weight: bold;
  cursor: pointer;
  border: none;
  border-radius: 0.2rem;
}

.options button:hover {
  background-color: rgb(231, 150, 0);
}

.result-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  font-weight: 400;
}

.result-card button {
  padding: 0.5rem 1rem;
  background-color: green;
  color: white;
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: 0.2rem;
  margin-bottom: 1rem;
}

.result-card button:hover {
  background-color: rgb(0, 80, 0);
}
@media screen and (max-width: 768px) {
  .quiz-content {
    width: 100%;
  }
}
